.react-spinner-loader-shadow {
  clear: left;
  padding-top: 1.5em;
}
.react-spinner-loader-shadow div {
  -webkit-filter: blur(1px);
  filter: blur(1px);
  float: left;
  width: 1em;
  height: .25em;
  border-radius: 50%;
  background: #e3dbd2;
}
.react-spinner-loader-shadow .react-spinner-loader-shadow-l {
  background: #d5d8d6;
}
.react-spinner-loader-shadow .react-spinner-loader-shadow-r {
  background: #eed3ca;
}
@-webkit-keyframes ball-l {
  0%, 50% {
    -webkit-transform: rotate(0) translateX(0);
    transform: rotate(0) translateX(0);
  }
  100% {
    -webkit-transform: rotate(50deg) translateX(-2.5em);
    transform: rotate(50deg) translateX(-2.5em);
  }
}
@keyframes ball-l {
  0%, 50% {
    -webkit-transform: rotate(0) translate(0);
    transform: rotate(0) translateX(0);
  }
  100% {
    -webkit-transform: rotate(50deg) translateX(-2.5em);
    transform: rotate(50deg) translateX(-2.5em);
  }
}
@-webkit-keyframes ball-r {
  0% {
    -webkit-transform: rotate(-50deg) translateX(2.5em);
    transform: rotate(-50deg) translateX(2.5em);
  }
  50%,
  100% {
    -webkit-transform: rotate(0) translateX(0);
    transform: rotate(0) translateX(0);
  }
}
@keyframes ball-r {
  0% {
    -webkit-transform: rotate(-50deg) translateX(2.5em);
    transform: rotate(-50deg) translateX(2.5em);
  }
  50%,
  100% {
    -webkit-transform: rotate(0) translateX(0);
    transform: rotate(0) translateX(0)
  }
}
@-webkit-keyframes shadow-l-n {
  0%, 50% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    opacity: .125;
    -webkit-transform: translateX(-1.57em);
    transform: translateX(-1.75em);
  }
}
@keyframes shadow-l-n {
  0%, 50% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    opacity: .125;
    -webkit-transform: translateX(-1.75);
    transform: translateX(-1.75em);
  }
}
@-webkit-keyframes shadow-r-n {
  0% {
    opacity: .125;
    -webkit-transform: translateX(1.75em);
    transform: translateX(1.75em);
  }
  50%,
  100% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
@keyframes shadow-r-n {
  0% {
    opacity: .125;
    -webkit-transform: translateX(1.75em);
    transform: translateX(1.75em);
  }
  50%,
  100% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
.react-spinner-loader-swing-l {
  -webkit-animation: ball-l .425s ease-in-out infinite alternate;
  animation: ball-l .425s ease-in-out infinite alternate;
}
.react-spinner-loader-swing-r {
  -webkit-animation: ball-r .425s ease-in-out infinite alternate;
  animation: ball-r .425s ease-in-out infinite alternate;
}
.react-spinner-loader-shadow-l {
  -webkit-animation: shadow-l-n .425s ease-in-out infinite alternate;
  animation: shadow-l-n .425s ease-in-out infinite alternate;
}
.react-spinner-loader-shadow-r {
  -webkit-animation: shadow-r-n .425s ease-in-out infinite alternate;
  animation: shadow-r-n .425s ease-in-out infinite alternate;
}


.react-spinner-loader-svg svg {
  -webkit-transform-origin: 50% 65%;
          transform-origin: 50% 65%;
}

.react-spinner-loader-svg svg polygon {
  stroke-dasharray: 17;
  -webkit-animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
          animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 136;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 136;
  }
}
@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.react-spinner-loader-svg-calLoader {
  width: 230px;
  height: 230px;
  -webkit-transform-origin: 115px 115px;
          transform-origin: 115px 115px;
  -webkit-animation: 1.4s linear infinite loader-spin;
          animation: 1.4s linear infinite loader-spin;
}

.react-spinner-loader-svg-cal-loader__path {

  -webkit-animation: 1.4s ease-in-out infinite loader-path;

          animation: 1.4s ease-in-out infinite loader-path;
}

@-webkit-keyframes loader-spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loader-spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes loader-path {
  0% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
  50% {
    stroke-dasharray: 0, 450, 10, 30, 10, 30, 10, 30, 10;
  }
  100% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
}
@keyframes loader-path {
  0% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
  50% {
    stroke-dasharray: 0, 450, 10, 30, 10, 30, 10, 30, 10;
  }
  100% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
}


#GSTM-app {
  margin-left: 5vw;
  margin-right: 5vw;
  font-family: "Source Sans Pro","Helvetica Neue",Helvetica,"Roboto",Arial,sans-serif;
}

#GSTM-app > div > div > p:nth-child(4) {
  width: 250px;
  position: relative;
}

#GSTM-app form {
  max-width: 525px;
  display: inline-block;
}

#GSTM-app form .ReportingCountries, .PartnerCountries, .TradeFlow, .ProductGroups, .FlowType {
  display: inline-block;
  min-width: 250px;
  margin-right: 10px;
  margin-bottom: 10px;
}

#GSTM-app form button {
  display: block;
  margin-top: 1rem;
  width: 20rem;
  font-size: 1em;
  line-height: 1;
  padding: 1.04rem;
  color: #fff;
  background-color: #0071bc;
  -webkit-appearance: none;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
}

#GSTM-app form button a {
  text-decoration: none;
  color: #fff;
}

#GSTM-app form p {
  margin-top: 5px;
  margin-bottom: 5px;
}

#GSTM-app form b {
  margin-top: 10px;
  margin-bottom: 0px;
}

#GSTM-app .GraphCollection {
  display: inline;
}

#GSTM-app .message {
  display: inline-block;
  vertical-align: top;
  margin-top: 200px;
  margin-left: 50px;
  color: #234781;
}

#GSTM-app .spinnerForForm {
  position: absolute;
  z-index: 1;
  vertical-align: top;
  margin-top: 150px;
  margin-left: 200px;
}

#GSTM-app .spinnerForCharts {
  display: inline-block;
  vertical-align: top;
  margin-top: 200px;
  margin-left: 50px;
}

#GSTM-app .Graph {
  display: inline-block;
  width: 525px;
  height: 400px;
  margin-top: 20px;
  margin-right: 50px;
}

#GSTM-app .Graph:first-of-type {
  margin-left: 30px;
}

#GSTM-app .Graph .ChartTitle {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 0;
}

#GSTM-app .Graph .caption {
  margin-top: 0;
  margin-bottom: 40px;
  margin-left: 40px;
  font-family: "Source Sans Pro","Helvetica Neue",Helvetica,"Roboto",Arial,sans-serif;
}

#GSTM-app .Graph button.modalOpen {
  background: none;
  color:#0071bc;
  border: none; 
  padding: 0;
  font: inherit;
  border-bottom: 1px solid #0071bc; 
  cursor: pointer;
}

#GSTM-app button#BackToTop {
  display: none;
  position: fixed;
  bottom: 30px;
  right: 30px;
  background: none;
  color:#0071bc;
  border: none; 
  padding: 0;
  font: inherit;
  border-bottom: 1px solid #0071bc; 
  cursor: pointer;
}

#GSTM-app footer {
  left: 50vw;
  width: 600px;
  text-align: center;
  margin: 30px auto 20px auto;
}

#GSTM-app footer b {
  margin-bottom: 5px;
}

#GSTM-app footer p {
  margin-top: 3px;
  margin-bottom: 3px;
}

#GSTM-app footer ol {
  margin-top: 5px;
}

#GSTM-app footer li {
  text-align: left;
  margin-top: 3px;
  margin-bottom: 3px;
}
